<template>
  <div class="wrapper-box">
    <!-- 类型筛选 -->
    <div class="sort">
      <span v-if="withOption" class="list" @click="$emit('clickOption')"></span>
      <div v-for="item in sorts" :key="item.id">
        <span @click="clickSort(item)" :class="item.id === activedSort ? 'actived' : ''">
          {{ item.name }}
          <span class="sort-icon">
            <img :src="item.id === activedSort && !item.byBig ? upIcon.active : upIcon.normal" />
            <img :src="item.id === activedSort && item.byBig ? downIcon.active : downIcon.normal" />
          </span>
        </span>
      </div>
    </div>
    <!-- 列表 -->
    <van-pull-refresh v-model="refreshing" @refresh="refresh">
      <template #pulling><refreshLoading text="下拉刷新"/></template>
      <template #loosing><refreshLoading text="下拉刷新"/></template>
      <template #loading><refreshLoading text="加载中"/></template>
      <template #success><refreshLoading text="加载完成"/></template>
      <van-list class="product-list" v-model="loading" :finished="finished" finished-text="没有更多啦~" @load="load">
        <!-- <lazy-component class="van-clearfix">
          <div
            class="item"
            v-for="(item, index) in products"
            :key="item.productId"
            :class="index % 2 === 0 ? 'mr' : ''"
            @click="toDetail(item)"
          >
            <img class="p-pic" :src="item.defaultImageUrl | imageToHttps | imageThumb('500') | imageToWebp($webpSurpport)" />
            <p class="p-desc">
              {{ item.productFullName | clipText }}
            </p>
            <p class="p-price">￥{{ item.finalPrice }}</p>
          </div>
        </lazy-component> -->
        <public-product-list :productList="products" :switchStyle='true'/>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import refreshLoading from '@/components/refreshLoading'
import mixin from './mixin'
import { getProductList, getAsyncPrice } from '@/http/service/smartChoice.js'
import ProductList from '@/components/publicProductList/index'
export default {
  mixins: [mixin],
  props: {
    withOption: {
      default: false,
    },
    info: {},
    cateId: {},
  },
  components: {
    refreshLoading,
    publicProductList: ProductList
  },
  data() {
    return {
      regionId: this.info || this.$route.query.regionId || this.$route.query.id,
      upIcon: {
        normal:
          'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAICAYAAADN5B7xAAAAAXNSR0IArs4c6QAAAINJREFUGBl1TzsOgCAMpSYsHoWd82hYOJEkMHAhd4/iwoA+Ugya2oS0vE/ynlLCpJRWPIFS9AVzzqaUsgPXWlvn3DFqXoYY43yTEBsWQWy99yf/1dQP3uHeXQzIENHGXFuPgTMvI4m71vrq0yINuRFJmrP3ISG3ZADW+iDSN/efAd3CBeBWKlZ4eQN9AAAAAElFTkSuQmCC',
        active:
          'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAICAYAAADN5B7xAAAAAXNSR0IArs4c6QAAAJFJREFUGBljYMAClFufJIAwFikGRnRBlbbnWv/+/jkNEmdiZjG9UyV5DVkNEzLHeOYzrv//fq9mYPjPBcIgNkgMWQ2Khg+v/0z7/59BC6YAxAaKTYXxQTRcA8jNQAXxyJIgNlAMxT9gPyDcDXIKNsD4DeYfRpAbgdaeRnYKVi2MDNcERFlMmdDdjU0xSAzqn2kART1KVjS8VrEAAAAASUVORK5CYII=',
      },
      downIcon: {
        normal:
          'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAICAYAAADN5B7xAAAAAXNSR0IArs4c6QAAAINJREFUGBljnDlz5gIGBoZ4ICYGLGQCqsoC4mtEqAapyWIEKZw3b57W79+/TwOZXCA+FvCNlZXVNCkp6RrIBgYQg5GRMRuLQrAQSA6kBsQBawAx0tLSFgCphSA2GlgIlQMLwzVAFaH7B+xuZAPAfkAWQPIPA8zdyPJY2bNmzUoAYWySAB1bJ40myOHYAAAAAElFTkSuQmCC',
        active:
          'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAICAYAAADN5B7xAAAAAXNSR0IArs4c6QAAAI5JREFUGBljVG55tOD/f4Z4BiIAIyPDQiYBUZYsIOMaIfUgNSC1TGfTpb4xMrGGMjAwfsOtiRGsBqSWCaToTpXkNUYmxmxcGkByIDUgebAGEONutcwCkBtBbGQAEgPJwcTgGkAC6P6BuRumGEQzInNAbJW251r//v45DWIzMbOYwpwC4uMEyq1PEkAYmwIAGAQowDyhBC4AAAAASUVORK5CYII=',
      },
      sorts: [
        { id: '1', name: '销量', byBig: true, flag: ['saleDesc', 'sale'] },
        { id: '2', name: '价格', byBig: false, flag: ['priceDesc', 'price'] },
      ],
      activedSort: '1',

      products: [],
      lastProducts: [],
      pageIndex: 1,
      pageSize: 10,
      shareId: '',
      refreshing: false,
      loading: false,
      finished: false,

    }
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    },
    getQs() {
      const item = this.sorts.find(v => {
        return v.id === this.activedSort
      })
      return item.byBig ? item.flag[0] : item.flag[1]
    },
  },
  watch: {
    cateId: {
      handler: function(v) {
        this.finished = false // 重置结束状态
        this.pageIndex = 1
        this.products = []
        this.lastProducts = []
        this.load()
      },
    },
  },
  methods: {
    clickSort(item) {
      if (item.id === this.activedSort) {
        // 没有切换排序类型
        item.byBig = !item.byBig
      } else {
        // 改变了排序类型
        this.activedSort = item.id
        // 始终让当前的类型按照某个顺序（从大到小）排序,其他的复位
        this.sorts.forEach(v => {
          v.byBig = false
        })
        item.byBig = true
      }
      this.finished = false // 重置结束状态
      this.pageIndex = 1
      this.load()
    },
    toDetail(item) {
      // console.log(item)
      this.$router.push({ path: '/goodsDetail', query: { productId: item.productId } })
    },
    refresh() {
      this.finished = false // 重置结束状态
      this.loading = false // 重置结束状态
      this.lastProducts = []
      this.pageIndex = 1 // 重置页数
      this.resetSort()
      this.load()
    },
    resetSort() {
      // 重置排序状态和qs参数
      this.activedSort = this.sorts[0].id
      this.sorts.forEach(v => (v.byBig = false))
      this.sorts[0].byBig = true
    },
    load() {
      if (this.finished) return // 如果已经finish，就不请求了
      let params = {
        pageIndex: this.pageIndex,
        pageSize: this.pageSize,
        storeMemberId: this.shareId, // 分享人或者访问店铺id
        productCateId: this.cateId || 0, // 类型id
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        districtId: this.location.districtId,
        streetId: this.location.townshipId,
        filterData: '', // 筛选条件
        qs: this.getQs, // 排序
        pageId: this.regionId,
      }
      getProductList(params)
        .then(res => {
          if (res.success) {
            if (res.totalCount < this.pageSize) this.finished = true
            let list = res.data.productList
            let traceId = res.data.traceId
            if (!list || list.length < 1) return
            return new Promise((resolve, reject) => {
              getAsyncPrice({
                traceId,
              }).then(
                res => {
                  if (res.success) {
                    resolve([list, res.products])
                  }
                },
                err => {
                  reject(err)
                }
              )
            })
          }
        })
        .then(res => {
          this.refreshing = false
          this.loading = false
          if (!res) return
          let [prodList, priceList] = res
          // console.log(prodList, priceList)
          if (prodList.length !== this.lastProducts.length || prodList[0].productId !== this.lastProducts[0].productId) {
            let arr = prodList.map((val, idx) => {
              val.finalPrice = priceList[idx].finalPrice // 合并price
              val.plusPrice = priceList[idx].plusPrice
              return val
            })
            this.pageIndex === 1 ? (this.products = arr) : this.products.push(...arr)
            this.pageIndex += 1
          }
          this.lastProducts = prodList
        })
    },
  },
  created() {
    this.shareId = localStorage.getItem('usi') || ''
    document.body.classList.add('nomore-repeat-load')
  },
  beforeDestroy() {
    document.body.classList.remove('nomore-repeat-load')
  }
}
</script>

