import URL from '../url.js'
import { get } from '../http'
import config from '../../config/index'

export const getProvinceInfo = data => get(URL.provinceInfo, data, {}, true)

export const getProductList = data =>
  get(URL.productList, data, {
    baseURL: config.SHOST,
  }, true)

export const getAsyncPrice = data =>
  get(URL.asyncPrice, data, {
    baseURL: config.SHOST,
  }, true)

export const getCategoryList = data => get(URL.cateList, data)

export const getCityCategoryList = (data, config = {}) => get('/sg/cms/chara/cityChara.json', data, config)
